.header-nav__links {
  list-style: none;
  display: flex;
  align-items: center;
  margin: 0;
  width: 100%;
  justify-content: space-between;
}

.header-nav__links_mobile {
  height: 248px;
  width: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.header-nav__title {
  font-family: 'Roboto Slab', serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  text-align: left;
  margin: 0;
  padding: 30px 0 26px 104px;
  color: white;  
  text-decoration: none;
}

.header-nav__title_dark {
  color: #1A1B22;
}

.header-nav__links-container {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
}

.header-nav__links-container_mobile {
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  padding: 0;
}

.header-nav__home {
  line-height: 4.4;
  text-decoration: none;
  text-align: center;
  height: 100%;
  width: 68px;
}

.header-nav__bottom-border {
  border-bottom: #FFFFFF 3px solid;
}

.header-nav__bottom-border_dark {
  border-bottom: #1A1B22 3px solid;
}

.header-nav__home-text {
  color: #FFFFFF;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  text-decoration: none;
}

.header-nav__home-text_dark {
  color: #1A1B22;
}

.header-nav__signin {
  color: #FFFFFF;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  text-align: center;
  width: 176px;
  height: 48px;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 100px;
  background: none;
  margin: 0 104px 0 54px;
}

.header-nav__signin-container {
  align-self: center;
}

.header-nav__saved-news {
  width: 178px;
  margin: 0 0 0 44px;
  height: 100%;
  text-align: center;
  line-height: 4.4;
}

.header-nav__saved-news-text {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  color: #1A1B22;
}

.header-nav__saved-news-text_light {
  color: #fff;
}

.header-nav__user-button {
  width: 112px;
  height: 48px;
  background: none;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 100px;
  margin: 0 104px 0 54px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  color: #fff;
  display: flex;
  justify-content: space-around;
  padding-top: 9px;
}

.header-nav__user-button_dark {
  color: #1A1B22;
  border: 1px solid #1A1B22;
}

@media screen and (max-width: 1024px) {
 
  .header-nav__title {
    font-size: 20px;
    line-height: 24px;
    margin: 0;
    padding-top: 23px;
    padding-bottom: 18px;
  }

  .header-nav__home {
    width: 68px;
    line-height: 4;
  }
  
  .header-nav__home-text {
    font-size: 16px;
  }

  .header-nav__signin {
    font-size: 16px;
    line-height: 24px;
    width: 152px;
    height: 40px;
    margin-left: 28px;
  }
  
  .header-nav__saved-news {
    width: 160px;
    margin: 0 0 0 16px;
    line-height: 4;
  }
  
  .header-nav__saved-news-text {
    font-size: 16px;
  }
  
  .header-nav__user-button {
    width: 100px;
    height: 40px;
    margin: 0 104px 0 16px;
    font-size: 16px;
    line-height: 24px;
    padding-top: 6px;
  }
}

.header-nav__menu {
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  height: 24px;
  width: 24px;
  position: absolute;
  top: 16px;
  right: 16px;
}

.header-nav__menu_type_burger {
  background-image: url('../../images/icons/menu-burger.png');
}

.header-nav__menu_type_burger-dark {
  background-image: url('../../images/icons/menu-burger-dark.png');
}

.header-nav__menu_type_close {
  background-image: url('../../images/icons/close.png');
}

@media screen and (max-width: 767.5px) {  
  
  .header-nav__title {
    font-size: 16px;
    line-height: 24px;
    padding: 16px 0 16px 16px;
  }

  .header-nav__home {
    height: auto;
    text-align: left;
    margin-top: 4px;
    padding-left: 16px;
  }
  
  .header-nav__bottom-border {
    border: none;
  }
  
  .header-nav__bottom-border_dark {
    border: none;
  }
  
  .header-nav__home-text {
    font-size: 18px;
    font-weight: 500;
    padding-left: 0;
    line-height: 56px;
  }
  
  .header-nav__saved-news {
    height: auto;
    text-align: left;
    line-height: 56px;
  }
  
  .header-nav__saved-news-text {
    font-size: 18px;
  }
  
  .header-nav__user-button {
    width: 288px  ;
    height: 56px;
    margin: 0;
    padding-top: 13px;
    margin-top: 14px;
    justify-content: center;
  }

  .header-nav__user-button-container {
    align-self: center;
  }

  .header-nav__signin {
    font-size: 18px;
    width: 288px;
    height: 56px;
    align-self: center; 
    margin: 14px 0 20px 0;
  }
  
  .header-nav__logout-icon {
    padding-left: 10px;
  }
}